    <div class="tool-header">
      <h1>üé® È¢úËâ≤ÈÄâÊã©Âô®</h1>
      <p>‰∏ì‰∏öÁöÑÈ¢úËâ≤ÈÄâÊã©ÂíåËΩ¨Êç¢Â∑•ÂÖ∑ÔºåÊîØÊåÅÂ§öÁßçÈ¢úËâ≤Ê†ºÂºè</p>
    </div>

    <!-- ÁßªÂä®Á´ØËßÜÂõæÂàáÊç¢ -->
    <div class="mobile-view-toggle" v-if="isMobile">
      <el-radio-group v-model="activeView" @change="handleViewChange">
        <el-radio-button label="picker">üé® ÈÄâÊã©Âô®</el-radio-button>
        <el-radio-button label="formats">üìã Ê†ºÂºè</el-radio-button>
        <el-radio-button label="palette">üé≠ Ë∞ÉËâ≤Êùø</el-radio-button>
      </el-radio-group>
    </div>

    <el-row :gutter="20">
      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12" class="picker-col" :class="{ 'mobile-hidden': isMobile && activeView !== 'picker' }">
        <el-card class="picker-card">
          <div slot="header">
            <span>È¢úËâ≤ÈÄâÊã©</span>
          </div>
          
          <div class="color-picker-section">
            <el-color-picker 
              v-model="selectedColor" 
              @change="handleColorChange"
              size="large"
              show-alpha
            ></el-color-picker>
            <div class="color-display" :style="{ backgroundColor: selectedColor }"></div>
          </div>

          <el-divider>Âø´ÈÄüÈÄâÊã©</el-divider>
          
          <div class="preset-colors">
            <div 
              v-for="color in presetColors" 
              :key="color"
              class="preset-color"
              :style="{ backgroundColor: color }"
              @click="selectPresetColor(color)"
              :title="color"
            ></div>
          </div>

          <el-divider>ËæìÂÖ•È¢úËâ≤ÂÄº</el-divider>

          <el-form label-width="80px">
            <el-form-item label="HEX">
              <el-input v-model="colorFormats.hex" @input="updateFromHex" placeholder="#FF0000"></el-input>
            </el-form-item>
            <el-form-item label="RGB">
              <el-input v-model="colorFormats.rgb" @input="updateFromRgb" placeholder="rgb(255, 0, 0)"></el-input>
            </el-form-item>
            <el-form-item label="HSL">
              <el-input v-model="colorFormats.hsl" @input="updateFromHsl" placeholder="hsl(0, 100%, 50%)"></el-input>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>

      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12" class="formats-col" :class="{ 'mobile-hidden': isMobile && activeView !== 'formats' }">
        <el-card class="info-card">
          <div slot="header">
            <span>È¢úËâ≤‰ø°ÊÅØ</span>
          </div>
          
          <el-descriptions :column="1" border>
            <el-descriptions-item label="HEX">
              <span class="color-value">{{ colorFormats.hex }}</span>
              <el-button size="mini" @click="copyToClipboard(colorFormats.hex)">Â§çÂà∂</el-button>
            </el-descriptions-item>
            <el-descriptions-item label="RGB">
              <span class="color-value">{{ colorFormats.rgb }}</span>
              <el-button size="mini" @click="copyToClipboard(colorFormats.rgb)">Â§çÂà∂</el-button>
            </el-descriptions-item>
            <el-descriptions-item label="RGBA">
              <span class="color-value">{{ colorFormats.rgba }}</span>
              <el-button size="mini" @click="copyToClipboard(colorFormats.rgba)">Â§çÂà∂</el-button>
            </el-descriptions-item>
            <el-descriptions-item label="HSL">
              <span class="color-value">{{ colorFormats.hsl }}</span>
              <el-button size="mini" @click="copyToClipboard(colorFormats.hsl)">Â§çÂà∂</el-button>
            </el-descriptions-item>
            <el-descriptions-item label="HSLA">
              <span class="color-value">{{ colorFormats.hsla }}</span>
              <el-button size="mini" @click="copyToClipboard(colorFormats.hsla)">Â§çÂà∂</el-button>
            </el-descriptions-item>
            <el-descriptions-item label="HSV">
              <span class="color-value">{{ colorFormats.hsv }}</span>
              <el-button size="mini" @click="copyToClipboard(colorFormats.hsv)">Â§çÂà∂</el-button>
            </el-descriptions-item>
            <el-descriptions-item label="CMYK">
              <span class="color-value">{{ colorFormats.cmyk }}</span>
              <el-button size="mini" @click="copyToClipboard(colorFormats.cmyk)">Â§çÂà∂</el-button>
            </el-descriptions-item>
          </el-descriptions>

          <el-divider>È¢úËâ≤ÂàÜÊûê</el-divider>

          <el-descriptions :column="1" border>
            <el-descriptions-item label="‰∫ÆÂ∫¶">{{ colorAnalysis.brightness }}%</el-descriptions-item>
            <el-descriptions-item label="ÂØπÊØîÂ∫¶">{{ colorAnalysis.contrast }}</el-descriptions-item>
            <el-descriptions-item label="Ëâ≤Ê∏©">{{ colorAnalysis.temperature }}</el-descriptions-item>
          </el-descriptions>
        </el-card>

        <el-card class="palette-card" style="margin-top: 20px;" :class="{ 'mobile-hidden': isMobile && activeView !== 'palette' }">
          <div slot="header">
            <span>Ë∞ÉËâ≤Êùø</span>
          </div>
          
          <div class="palette-section">
            <h4>‰∫íË°•Ëâ≤</h4>
            <div class="color-palette">
              <div 
                v-for="color in colorPalettes.complementary" 
                :key="color"
                class="palette-color"
                :style="{ backgroundColor: color }"
                @click="selectPresetColor(color)"
                :title="color"
              ></div>
            </div>
          </div>

          <div class="palette-section">
            <h4>Á±ª‰ººËâ≤</h4>
            <div class="color-palette">
              <div 
                v-for="color in colorPalettes.analogous" 
                :key="color"
                class="palette-color"
                :style="{ backgroundColor: color }"
                @click="selectPresetColor(color)"
                :title="color"
              ></div>
            </div>
          </div>

          <div class="palette-section">
            <h4>‰∏âËßíËâ≤</h4>
            <div class="color-palette">
              <div 
                v-for="color in colorPalettes.triadic" 
                :key="color"
                class="palette-color"
                :style="{ backgroundColor: color }"
                @click="selectPresetColor(color)"
                :title="color"
              ></div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { ColorUtil } from '@/util/ColorUtil'
import { ClipboardUtil } from '@/util/ClipboardUtil'
import DeviceUtil from '@/util/DeviceUtil'

export default {
  name: 'ColorPickerView',
  metaInfo: {
    title: 'È¢úËâ≤ÈÄâÊã©Âô® - Âú®Á∫øÈ¢úËâ≤ËΩ¨Êç¢Â∑•ÂÖ∑',
    meta: [
      { name: 'description', content: '‰∏ì‰∏öÁöÑÂú®Á∫øÈ¢úËâ≤ÈÄâÊã©Âô®ÔºåÊîØÊåÅHEX„ÄÅRGB„ÄÅHSL„ÄÅCMYKÁ≠âÂ§öÁßçÈ¢úËâ≤Ê†ºÂºèËΩ¨Êç¢ÔºåÊèê‰æõË∞ÉËâ≤ÊùøÂíåÈ¢úËâ≤ÂàÜÊûêÂäüËÉΩ„ÄÇ' },
      { name: 'keywords', content: 'È¢úËâ≤ÈÄâÊã©Âô®,È¢úËâ≤ËΩ¨Êç¢,HEXËΩ¨RGB,Ë∞ÉËâ≤Êùø,È¢úËâ≤Â∑•ÂÖ∑' }
    ]
  },
  data() {
    return {
      selectedColor: '#FF0000',
      colorFormats: {
        hex: '#FF0000',
        rgb: 'rgb(255, 0, 0)',
        rgba: 'rgba(255, 0, 0, 1)',
        hsl: 'hsl(0, 100%, 50%)',
        hsla: 'hsla(0, 100%, 50%, 1)',
        hsv: 'hsv(0, 100%, 100%)',
        cmyk: 'cmyk(0%, 100%, 100%, 0%)'
      },
      colorAnalysis: {
        brightness: 33,
        contrast: 'High',
        temperature: 'Warm'
      },
      colorPalettes: {
        complementary: [],
        analogous: [],
        triadic: []
      },
      presetColors: [
        '#FF0000', '#FF8000', '#FFFF00', '#80FF00', '#00FF00', '#00FF80',
        '#00FFFF', '#0080FF', '#0000FF', '#8000FF', '#FF00FF', '#FF0080',
        '#000000', '#404040', '#808080', '#C0C0C0', '#FFFFFF', '#800000',
        '#808000', '#008000', '#008080', '#000080', '#800080', '#FFA500'
      ],
      isMobile: false,
      activeView: 'picker'
    }
  },
  mounted() {
    this.updateColorFormats()
    this.generatePalettes()
    this.checkMobile()
    window.addEventListener('resize', this.checkMobile)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.checkMobile)
  },
  methods: {
    checkMobile() {
      this.isMobile = DeviceUtil.isMobile()
    },

    handleViewChange() {
      // ÁßªÂä®Á´ØËßÜÂõæÂàáÊç¢Êó∂ÁöÑÂ§ÑÁêÜ
    },

    handleColorChange(color) {
      this.selectedColor = color
      this.updateColorFormats()
      this.generatePalettes()
    },

    selectPresetColor(color) {
      this.selectedColor = color
      this.updateColorFormats()
      this.generatePalettes()
    },

    updateColorFormats() {
      const rgb = ColorUtil.hexToRgb(this.selectedColor)
      if (!rgb) return

      this.colorFormats.hex = this.selectedColor.toUpperCase()
      this.colorFormats.rgb = `rgb(${rgb[0]}, ${rgb[1]}, ${rgb[2]})`
      this.colorFormats.rgba = `rgba(${rgb[0]}, ${rgb[1]}, ${rgb[2]}, 1)`

      const hsl = ColorUtil.rgbToHsl(rgb[0], rgb[1], rgb[2])
      this.colorFormats.hsl = `hsl(${hsl.h}, ${hsl.s}%, ${hsl.l}%)`
      this.colorFormats.hsla = `hsla(${hsl.h}, ${hsl.s}%, ${hsl.l}%, 1)`

      const hsv = ColorUtil.rgbToHsv(rgb[0], rgb[1], rgb[2])
      this.colorFormats.hsv = `hsv(${hsv.h}, ${hsv.s}%, ${hsv.v}%)`

      const cmyk = ColorUtil.rgbToCmyk(rgb[0], rgb[1], rgb[2])
      this.colorFormats.cmyk = `cmyk(${cmyk.c}%, ${cmyk.m}%, ${cmyk.y}%, ${cmyk.k}%)`

      this.updateColorAnalysis({ r: rgb[0], g: rgb[1], b: rgb[2] })
    },

    updateColorAnalysis(rgb) {
      // ËÆ°ÁÆó‰∫ÆÂ∫¶
      const brightness = (rgb.r * 299 + rgb.g * 587 + rgb.b * 114) / 1000
      this.colorAnalysis.brightness = Math.round((brightness / 255) * 100)

      // Âà§Êñ≠ÂØπÊØîÂ∫¶
      this.colorAnalysis.contrast = brightness > 128 ? 'High' : 'Low'

      // Âà§Êñ≠Ëâ≤Ê∏©
      if (rgb.r > rgb.b) {
        this.colorAnalysis.temperature = 'Warm'
      } else if (rgb.b > rgb.r) {
        this.colorAnalysis.temperature = 'Cool'
      } else {
        this.colorAnalysis.temperature = 'Neutral'
      }
    },

    generatePalettes() {
      const rgb = ColorUtil.hexToRgb(this.selectedColor)
      if (!rgb) return

      const hsl = ColorUtil.rgbToHsl(rgb[0], rgb[1], rgb[2])

      // ‰∫íË°•Ëâ≤
      const complementaryHue = (hsl.h + 180) % 360
      this.colorPalettes.complementary = [
        this.selectedColor,
        ColorUtil.hslToHex(complementaryHue, hsl.s, hsl.l)
      ]

      // Á±ª‰ººËâ≤
      this.colorPalettes.analogous = [
        ColorUtil.hslToHex((hsl.h - 30 + 360) % 360, hsl.s, hsl.l),
        this.selectedColor,
        ColorUtil.hslToHex((hsl.h + 30) % 360, hsl.s, hsl.l)
      ]

      // ‰∏âËßíËâ≤
      this.colorPalettes.triadic = [
        this.selectedColor,
        ColorUtil.hslToHex((hsl.h + 120) % 360, hsl.s, hsl.l),
        ColorUtil.hslToHex((hsl.h + 240) % 360, hsl.s, hsl.l)
      ]
    },

    updateFromHex() {
      if (ColorUtil.isValidHex(this.colorFormats.hex)) {
        this.selectedColor = this.colorFormats.hex
        this.updateColorFormats()
        this.generatePalettes()
      }
    },

    updateFromRgb() {
      const rgb = ColorUtil.parseRgb(this.colorFormats.rgb)
      if (rgb) {
        this.selectedColor = ColorUtil.rgbToHex(rgb.r, rgb.g, rgb.b)
        this.updateColorFormats()
        this.generatePalettes()
      }
    },

    updateFromHsl() {
      const hsl = ColorUtil.parseHsl(this.colorFormats.hsl)
      if (hsl) {
        this.selectedColor = ColorUtil.hslToHex(hsl.h, hsl.s, hsl.l)
        this.updateColorFormats()
        this.generatePalettes()
      }
    },

    async copyToClipboard(text) {
      try {
        const success = await ClipboardUtil.copyText(text)
        if (success) {
          this.$message.success('Â∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø')
        } else {
          this.$message.error('Â§çÂà∂Â§±Ë¥•')
        }
      } catch (error) {
        this.$message.error('Â§çÂà∂Â§±Ë¥•: ' + error.message)
      }
    }
  }
}
</script>

